/* FUCK trying to style BS react components. CHROME -> copy selector FTW */

.navbar-default .navbar-toggle .icon-bar, .navbar-collapse.collapse.in>ul>li.dropdown.open>ul>li>a {
    color: white;
}

/* Hamburger icon */

#root>div>div>div>nav>div>div.navbar-header>button>span {
    background-color: white;
}

/* Dropdown background */

#root>div>div>div>nav>div>div.navbar-header>button:hover, #root>div>div>div>nav>div>div.navbar-header>button:focus {
    background-color: #00AED6;
}

/* Dropdown menu items */

#root>div>div>div>nav>div>div.navbar-collapse.collapse.in>ul>li.dropdown>ul>li>a {
    color: white;
}
.footer {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    background-color: white;
    box-shadow: 0 -1px 30px #000080;
    padding: 3px;
    height: 40px;
}

.hCenter {
    margin: 8px auto;
    width: 97px;
    height: 100%;
}

.controlBox {
    display: inline;
    padding: 6px 7px;
    /* border: 2px solid gray; */
}

.controlBox:hover {
    cursor: pointer;
    background-color: grey;
}

.controlBox:first-child {
    border: 2px solid grey;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
}

.controlBox:nth-child(2) {
    border-top: 2px solid grey;
    border-bottom: 2px solid grey;
}

#volume-button {
    border: 2px solid grey;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
}

#volume-slider {
    padding: 12px 7px;
    background-color: white;
    border-radius: 10px;
    position: absolute;
    height: 150px;
    width: 32px;
    bottom: 3px;
    margin-left: 60px;
    border: 2px solid #000080;
    display: none;
}

#volume-button:hover~#volume-slider {
    display: block;
}

#volume-slider:hover {
    display: block;
}
.album-art {
    height: 120px;
    width: auto;
}

.playing {
    background-color: lightgray;
}

.song:hover {
    background-color: darkgray;
    cursor: pointer;
}
#song-container {
    margin: 0 auto;
    margin-top: 70px;
    width: 98%;
    overflow-y: auto;
    background-color: white;
    border-radius: 1px;
    box-shadow: 0 0 8px 0 #000080;
}

html {
  height: 100%;
  background-image: -webkit-linear-gradient(#0F87F5, #3D52FF);
  background-image: linear-gradient(#0F87F5, #3D52FF);
}

body {
  background-color: rgba(0, 0, 0, 0);
}
